// components/ContactSectionUs.jsx
import { useState } from "react";
import styles from "./ContactSectionUs.module.css";

const ContactSectionUs = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: "",
    phone: "",
  });

  const [submitStatus, setSubmitStatus] = useState(null); // null, 'success', or 'error'

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevState) => ({
      ...prevState,
      [name]: value,
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    // Simple validation
    if (!formData.name || !formData.email || !formData.message) {
      setSubmitStatus("error");
      return;
    }

    // Simulate form submission
    console.log("Form submitted:", formData);

    // Simulate API call
    setTimeout(() => {
      setSubmitStatus("success");
      // Reset form after successful submission
      setFormData({
        name: "",
        email: "",
        message: "",
        phone: "",
      });

      // Reset status after 5 seconds
      setTimeout(() => setSubmitStatus(null), 5000);
    }, 1000);
  };

  return (
    <section className={styles.contactSection} id="contact">
      <div className={styles.container}>
        <div className={styles.contactHeader}>
          <h1 className={styles.companyName}>Limitless Grz</h1>
          <p className={styles.tagline}>Image is everything</p>
          <p className={styles.description}>
            Full-service graphics for vet glass. In-house design and
            fabrication.
          </p>
        </div>

        <div className={styles.contactContent}>
          <div className={styles.contactInfo}>
            <h2>Get in touch</h2>
            <ul>
              <li>
                <strong>CALL</strong> +1 (555) 123-4567
              </li>
              <li>
                <strong>EMAIL</strong> hello@limitlessgrz.com
              </li>
              <li>
                <strong>VISIT</strong> 401 Design Avenue, Creative District, CA
                90210
              </li>
            </ul>

            <p
              style={{
                marginTop: "2rem",
                fontStyle: "italic",
                color: "#7f8c8d",
              }}
            >
              We specialize in custom graphics solutions for veterinary glass
              and signage. Our team is ready to bring your vision to life with
              our in-house design and fabrication services.
            </p>
          </div>

          <div className={styles.contactForm}>
            <form onSubmit={handleSubmit}>
              <div className={styles.formGroup}>
                <label htmlFor="name">Name:</label>
                <input
                  type="text"
                  id="name"
                  name="name"
                  placeholder="Your full name"
                  value={formData.name}
                  onChange={handleChange}
                  required
                />
              </div>

              <div className={styles.formGroup}>
                <label htmlFor="email">Email:</label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  placeholder="you@example.com"
                  value={formData.email}
                  onChange={handleChange}
                  required
                />
              </div>

              <div className={styles.formGroup}>
                <label htmlFor="message">Message:</label>
                <textarea
                  id="message"
                  name="message"
                  placeholder="Tell us about your project..."
                  value={formData.message}
                  onChange={handleChange}
                  required
                />
              </div>

              <div className={styles.formGroup}>
                <label htmlFor="phone">Phone (optional):</label>
                <div className={styles.phoneInput}>
                  <span className={styles.phonePrefix}>+1</span>
                  <input
                    type="tel"
                    id="phone"
                    name="phone"
                    placeholder="555-123-4567"
                    value={formData.phone}
                    onChange={handleChange}
                  />
                </div>
              </div>

              <button type="submit" className={styles.submitBtn}>
                Send Message
              </button>

              {submitStatus === "success" && (
                <div className={styles.successMessage}>
                  Thank you for your message! We'll get back to you soon.
                </div>
              )}

              {submitStatus === "error" && (
                <div className={styles.errorMessage}>
                  Please fill in all required fields.
                </div>
              )}
            </form>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ContactSectionUs;
